import axios from 'axios';

const baseURL = process.env.VUE_APP_BASE_URL;

export function listarUnidades(codempresa) {
    return axios.get(`${baseURL}unidades/list?codempresa=${codempresa}`)
}

export function buscarUnidad(idUnidad) {
    return axios.get(`${baseURL}unidades/search?idUnidad=${idUnidad}`)
}

export function crearUnidades(placa, codcisterna, capacidad, codempresa) {
    return axios.post(`${baseURL}unidades/create`, {
        placa: placa,
        codcisterna: codcisterna,
        capacidad: capacidad,
        codempresa: codempresa
    })
}

export function eliminarUnidades(idUnidad) {
    return axios.delete(`${baseURL}unidades/delete?idUnidad=${idUnidad}`)
}

export function editarUnidades(placa, codcisterna, capacidad, id) {
    return axios.put(`${baseURL}unidades/edit`,{
        placa: placa,
        codcisterna: codcisterna,
        capacidad: capacidad,
        id: id
    })
}