<template>
    <div class="fixed inset-0 flex items-center justify-center z-50">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl min-w-[350px] max-w-[450px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Editar cisterna</h3>
                <ItemForm @value-input="savePlaca" label="Placa" name="placa" placeholder="Inserte la placa" :value="placa"/>
                <ItemForm @value-input="saveCodcisterna" label="Codigo cisterna" name="codcisterna" placeholder="Inserte un codigo para la cisterna" :value="codcisterna"/>
                <ItemForm @value-input="saveCapacidad" label="Capacidad" name="capacidad" placeholder="Inserte su capacidad" :value="capacidad"/>
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Cerrar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Actualizar</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import {ref, onMounted} from 'vue';
import ItemForm from '../generales/ItemForm.vue';
import {buscarUnidad} from '../../services/UnidadesService';

export default {
    name: 'ModalEditar',
    components: {ItemForm},
    props:{
        idUnidad: String
    },
    emits: ['cancelar-editar', 'save-datos'],
    setup(props, { emit }) {
        const placa = ref('');
        const codcisterna = ref('');
        const capacidad = ref('');
        const closeModal = () =>{
            emit('cancelar-editar')
        }
        const savePlaca = (value) =>{
            placa.value = value;
        }
        const saveCodcisterna = (value) =>{
            codcisterna.value = value;
        }
        const saveCapacidad = (value) => {
            capacidad.value = value;
        }
        const saveDatos = () => {
            emit('aceptar-editar',{
                id: props.idUnidad,
                placa: placa.value,
                codcisterna: codcisterna.value,
                capacidad: capacidad.value,
            })
        }
        onMounted(()=>{
            buscarUnidad(props.idUnidad)
                .then(response => {
                    placa.value = response.data.unidad[0].placa;
                    capacidad.value = response.data.unidad[0].capacidad;
                    codcisterna.value = response.data.unidad[0].codcisterna;
                })
        })
        return {
            closeModal,
            savePlaca,
            saveCodcisterna,
            saveCapacidad,
            saveDatos,
            placa,
            capacidad,
            codcisterna
        }
    },
};
</script>

