<template>
    <table class="w-full text-xs text-left text-gray-800">
        <thead class="text-xs text-gray-700 uppercase bg-gray-100">
            <tr>
                <th scope="col" class="px-6 py-3">
                    Placa
                </th>
                <th scope="col" class="px-6 py-3">
                    Codigo cisterna
                </th>
                <th scope="col" class="px-6 py-3">
                    Capacidad
                </th>
                <th scope="col" class="px-6 py-3">
                    Codigo empresa
                </th>
                <th scope="col" class="px-6 py-3">
                    Acciones
                </th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="unidad in listUnidades" :key="unidad.id"
                class="bg-white border-b hover:bg-gray-50 ease-in duration-300">
                <td class="px-6 py-3">
                    {{ unidad.placa }}
                </td>
                <td class="px-6 py-3">
                    {{ unidad.codcisterna }}
                </td>
                <td class="px-6 py-3">
                    {{ unidad.capacidad }}
                </td>
                <th scope="row" class="px-6 py-3 font-medium text-gray-900 whitespace-nowrap">
                    {{ unidad.codempresa }}
                </th>
                <td class="px-6 py-2 flex gap-2">
                    <button
                        class="font-medium py-[0.5rem] px-3 rounded-lg bg-green-500 hover:bg-green-600 ease-in duration-300 text-white"
                        @click.prevent="editarUnidad(unidad.id)">Editar</button>
                    <button
                        class="font-medium py-[0.5rem] px-3 rounded-lg bg-red-500 hover:bg-red-600 ease-in duration-300 text-white "
                        @click.prevent="eliminarUnidad(unidad.id)">Eliminar</button>
                </td>
            </tr>
        </tbody>
    </table>
</template>
<script>
export default ({
    name: "TableUnidades",
    props: {
        listUnidades: Array
    },
    methods: {
        eliminarUnidad(idUnidad) {
            this.$emit('eliminar-unidad', idUnidad)
        },
        editarUnidad(idUnidad) {
            this.$emit('editar-unidad', idUnidad)
        }
    }
})
</script>