<template>
    <div class="fixed inset-0 flex items-center justify-center z-50">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl min-w-[350px] max-w-[450px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Crear cisterna</h3>
                <ItemForm @value-input="savePlaca" label="Placa" name="placa" placeholder="Inserte la placa" value=""/>
                <ItemForm @value-input="saveCodcisterna" label="Codigo cisterna" name="codcisterna" placeholder="Inserte un codigo para la cisterna" value=""/>
                <ItemForm @value-input="saveCapacidad" label="Capacidad" name="capacidad" placeholder="Inserte su capacidad"/>
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Cerrar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-8 rounded-xl transition duration-300">Crear</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import {ref} from 'vue';
import ItemForm from '../generales/ItemForm.vue';
export default {
    name: 'ModalCreate',
    components: {ItemForm},
    emits: ['close-modal', 'save-datos'],
    setup(_, { emit }) {
        const placa = ref('');
        const codcisterna = ref('');
        const capacidad = ref('');
        const closeModal = () =>{
            emit('close-modal')
        }
        const savePlaca = (value) =>{
            placa.value = value;
        }
        const saveCodcisterna = (value) =>{
            codcisterna.value = value;
        }
        const saveCapacidad = (value) => {
            capacidad.value = value;
        }
        const saveDatos = () => {
            emit('save-datos',{
                placa: placa.value,
                codcisterna: codcisterna.value,
                capacidad: capacidad.value,
            })
        }
        return {
            closeModal,
            savePlaca,
            saveCodcisterna,
            saveCapacidad,
            saveDatos
        }
    },
};
</script>

